@charset "UTF-8";
.user-card {
  margin: 5px;
  padding: 10px;
  height: 350px;
  border: 2px solid #ef7352;
  border-radius: 10px;
  background-color: #fbda6c;
  opacity: 0;
  /*Элемент полностью прозрачный (невидимый)*/
  transition: 0.5s;
  /*Скорость перехода состояния элемента*/
  animation: show 0.5s 1;
  /* Указываем название анимации, её время и количество повторов*/
  animation-fill-mode: forwards;
  /* Чтобы элемент оставался в конечном состоянии анимации */
  animation-delay: 0.5s;
  /* Задержка перед началом */ }
  .user-card:hover {
    box-shadow: 0px 0px 10px 0px #ef7352;
    cursor: pointer; }
  .user-card .name,
  .user-card p {
    text-align: center; }
  .user-card img {
    width: 200px;
    margin: 10px 0; }
  .user-card .user-card__info {
    list-style: none; }

@keyframes show {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
